/**
 * Headroom.js
 */
let headroom = null;
if (Headroom.cutsTheMustard) {
    let myElement = document.getElementById("my--header");
    headroom = new Headroom(myElement, {
        "offset": 300,
        "tolerance": 5
    });
}
export { headroom };

