import { db } from "../log";

/**
 * 
 * 
 */
export let MY_console = function () {
    let purple = "font-weight: 900;color: #9b4dca;font-size: 15px";
    let yellow = "font-weight: 900;color: #ffb700;font-size: 15px";
    db("");
    console.log("\n" +
        "%c                  %c________\n%c" +
        "          %c| ERSS |\n%c" +
        "          %c|  |\n%c" +
        "          %c|______|\n%c" +
        "  \n" +
        "  \n" +
        "       " +
        "\n", purple, yellow, purple, yellow, purple, yellow, purple, yellow, purple); // 
    fetch("https://api.github.com/repos/xhemj/mingyan/commits")
        .then(r => r.json())
        .then(function (res) {
            db(`${res[0]["sha"].slice(0, 7)}`);
        });
};