import { MY_rankingApi } from "./rankingApi";
import { qs } from "../tools";

/**
 * 2021
 */
export let MY_ranking = function () {
    if (new Date().getTime() < 1609430400000 /* 2021-01-01 00:00:00 */) return;
    if (navigator.userAgent.toString().indexOf("bot") != -1 && navigator.userAgent.toString().indexOf("spider") != -1 /*  */) return;
    if (localStorage.getItem("___mingyan_2021_ranking_data__")) return;
    if (qs("force_action") == "skip_ranking" || qs("do") == "sr") {
        localStorage.setItem("___mingyan_2021_ranking_data__", "__SKIP__");
        return;
    }
    let id = null;
    let ip = null;
    let num = null;
    MY_rankingApi.getIp() // ip
        .then(function (ip_data) {
            // db(ip_data.ip);
            ip = ip_data.ip;
            // 
            // db("");
            localStorage.setItem("___mingyan_2021_ranking_data__", `__${ip}__`);  // 
            return MY_rankingApi.add("", ip);
        }) // 
        .then(function (add_data) {
            localStorage.setItem("___mingyan_2021_ranking_data__", `__${ip}__`);  // 
            id = add_data.result.res.id; // id
            return MY_rankingApi.getNum();
        })
        .then(function (num_data) { // 
            num = num_data["result"]["res1"]["data"][0]["num"];
            // 
            return swal({
                title: `${num}`,
                text: `2021${num}`, // 2021-01-26
                icon: "success",
                content: {
                    element: "input",
                    attributes: {
                        placeholder: "",
                        type: "text"
                    }
                },
                closeOnClickOutside: false
            });
        })
        .then(function (name) {
            if (name) { // 
                return MY_rankingApi.update(id, name, num);
            } else {
                location.href = "./";
            }
        })
        .then(function () {
            // db(update_data);
            location.href = "./";
        })
        .catch(function (e) {
            console.error(e);
        });
};