import { mingyan } from "../../mingyan";
import { $main, $myInfo, $page, hashName, ua } from "../init";
import { db } from "../log";
import { htmlTemp } from "../templete";
import { _hmt } from "../tongji";
import { randomNumber, qs, loadJs } from "../tools";
import { MY_checkConj } from "./checkConj";
import { MY_checkPic } from "./checkPic";
import { checkProbability } from "./checkProbability";
import { MY_initfancybox } from "./initFancybox";
import { MY_loadMyType } from "./showFrom";

window["skipCheckHash"] = window["skipCheckHash"] || false;

/**
 * 
 * @param {Num} id ID
 */
export let MY_show = function (id) {
    // db(mingyan);
    // db(mingyanPicUrl);
    // db(specialVerbList);

    // Fancybox
    MY_initfancybox();

    // 
    setTimeout(() => {
        MY_loadMyType();
    }, 1);

    let _id = function (id) {
        if (id) return id;
        if (qs("id") != "") return qs("id"); // ?id=xxID
        if (location.hash != "" && hashName[location.hash] != true /* hash */) return location.hash.replace("#", "");
        if (localStorage.getItem("___mingyan_id__")) return localStorage.getItem("___mingyan_id__"); // id
        return randomNumber(0, mingyan.length - 1); // 
    };

    try {
        // 
        if (location.hash == "" && hashName[location.hash] != true /* hash */) $($page).hide();
        $($main).hide();
        if (mingyan.length != 0) {
            // db("");
            let n = _id(id);
            let _my = mingyan[n];
            // _my = _checkMingyan(_my); // 
            if (hashName[location.hash]) return; // hash
            // hash
            window["skipCheckHash"] = true;
            location.hash = "#" + n;
            window["skipCheckHash"] = false; // 
            // 
            localStorage.setItem("___mingyan_id__", n);
            let name = _my.split("")[0];
            // db(name);
            let my = _my.split("")[1] + (_my.split("")[2] ? "" : "") + (_my.split("")[2] || "");
            // db("" + n + "" + my);
            _hmt.push(["_trackEvent", "", "", "", name + "" + my]);
            //  2021-01-24
            $($myInfo).html(htmlTemp.MY_INFO);
            /**
             * 
             */
            $("span.my--mingyan-text").html(MY_checkPic(name + "" + my)); // 
            // 
            if (ua.device == "PC") {
                $(".my--mingyan-pic").css({
                    "min-width": 28 * my.split("").length - 50, // 28px
                    "margin-left": "25px",
                    "margin-right": "25px"
                });
            } else {
                $(".my--mingyan-pic").css({
                    "min-width": 28 * my.split("").length - 50, // 28px
                    "margin-left": "5px",
                    "margin-right": "5px"
                });
            }
            /**
             * 
             */
            let verb = MY_checkConj(name + "" + my);  // 
            $("span.my--mingyan-verb").html(verb);

            $("span.my--mingyan-name").text(name);
            $($main).fadeIn();
            // 
            $("#reload").attr("href", "#" + checkProbability());

            if ($(".my--mingyan-text").text().indexOf("") != -1) { // 
                loadJs("https://cdn.jsdelivr.net/npm/aplayer@1.10.0/dist/APlayer.min.js");
            }
            // let title = "ERSS  ERSS " + my;
            // if (navigator.userAgent.toString().indexOf("bot") == -1 && navigator.userAgent.toString().indexOf("spider") == -1 /*  */) {
            //     document.title = title;
            //     let description = name + verb + "" + my;
            //     $("meta[name='description']").attr("content", description);
            //     $("meta[property='og:description']").attr("content", description);
            //     $("meta[name='og:description']").attr("content", description);
            //     $("meta[property='og:title']").attr("content", title);
            //     $("meta[name='og:title']").attr("content", title);
            // }
            lazyload(); // 
        } else {
            return false;
        }
    } catch (err) {
        // 
        if (sessionStorage.getItem("___mingyan_is_special_mode__")) location.href = "#0";
        console.error("");
        $(".my--mingyan-text").text("");
        $($myInfo).html("</br>" + err + "</br><a id=\"reload\"></a>");
        $($main).fadeIn();
        $($myInfo).fadeIn();
        db(err);
        //location.href = "http://" + location.hostname + ":" + location.port + location.pathname;
    }
};