import { apiUrls } from "../init";
import { db } from "../log";

// tcb
let app = null;
// 
let auth = null;
async function login() {
    await auth.anonymousAuthProvider().signIn();
    let loginState = await auth.getLoginState();
    // db("");
    db(loginState);
}
app = cloudbase.init({ env: "xhemj-0gjckebwf7276129" });
auth = app.auth();
login(); // 

// 
export let MY_rankingApi = {
    // 
    add: function (name, ip) {
        if (!Promise) return;
        return new Promise(function (resolve, reject) {
            app
                .callFunction({
                    name: "mingyan",
                    data: {
                        event: "add",
                        name: name.toString(),
                        ip: ip,
                        ua: navigator.userAgent.toString() || ""
                    }
                })
                .then((res) => {
                    // db("");
                    // db(res);
                    resolve(res);
                })
                .catch(function (e) {
                    reject(e);
                });
        });
    },
    // 
    update: function (id, name, num) {
        if (!Promise) return;
        return new Promise(function (resolve, reject) {
            app
                .callFunction({
                    name: "mingyan",
                    data: {
                        event: "update",
                        id: id,
                        data: {
                            "name": name
                        },
                        "num": num
                    }
                })
                .then((res) => {
                    // db("");
                    // db(res);
                    resolve(res);
                })
                .catch(function (e) {
                    reject(e);
                });
        });

    },
    // IP
    getIp: function () {
        if (!Promise) return;
        return new Promise(function (resolve, reject) {
            $.getJSON(apiUrls.ip,
                function (json) {
                    // db("");
                    let res = {
                        "ip": json.ip
                    };
                    resolve(res);
                }).catch(function (e) {
                reject(e);
            });
        });
    },
    //
    getNum: function () {
        if (!Promise) return;
        return new Promise(function (resolve, reject) {
            app
                .callFunction({
                    name: "mingyan",
                    data: {
                        event: "getnum"
                    }
                })
                .then((res) => {
                    // db("");
                    // db(res);
                    resolve(res);
                })
                .catch(function (e) {
                    reject(e);
                });

        });
    }
};