/**
 * fancybox
 */
export let MY_initfancybox = function () {
    setTimeout(() => {
        $(".fancybox").fancybox({
            buttons: [
                "zoom",
                "share",
                "slideShow",
                "fullScreen",
                "download",
                "close"
            ],
            lang: "zh-cn",
            i18n: {
                "zh-cn": {
                    CLOSE: "",
                    NEXT: "",
                    PREV: "",
                    ERROR: " <br/> ",
                    FULL_SCREEN: "",
                    THUMBS: "",
                    DOWNLOAD: "",
                    SHARE: "",
                    ZOOM: ""
                }
            }
        });
    }, 500);
};