import { specialVerbList } from "../../mingyan";

/**
 *  
 * @param {String} my 
 */
export let  MY_checkConj = function (my) {
    my = my.split("")[1];
    if (specialVerbList[my]) {
        let text = specialVerbList[my];
        return text;
    } else {
        return "";
    }
};