import { mingyan } from "../../mingyan";
import { $page, $main, $search, $footer } from "../init";
import { MY_initLogo as _initLogo } from "./initLogo";
import { ___date_version___ } from "./config";

import MORE_MD from "../../page/more.md";

/**
 * Markdown
 * @param {String} id id
 * @param {String} url Markdown
 */
let _mdToHtml = function (id, name) {
    $($page).hide();
    $($main).hide();
    $($search).hide();
    $(id).html("<strong>" + "<div style=\"text-align: center\" class=\"mdui-ripple\"><style>h1{font-size:30px}</style><h1></h1></div></strong></br></br></br></br>");
    marked.setOptions({
        breaks: true
    });
    let html = marked(name); // marked.js
    html = html.replace(/<a /g, "<a target=\"_blank\" rel=\"nofollow ugc\" "); // 
    $(id).html("<strong>" + html + "</strong></br></br></br></br>");  // 
    $(id).fadeIn();
    $($footer).html("" + mingyan.length + "</br><a class=\"aline\" id=\"clearhash_btn\" href=javascript:;><\/a>"); // footer
    _initLogo();
    $($page).fadeIn();
};

/**
 * 
 */
export let MY_page = {
    "more": function () {
        return _mdToHtml($page, MORE_MD);
    },
    "about": function () {
        return _mdToHtml($page, "./src/page/about.md?t=" + ___date_version___);
    },
    "contribute": function () {
        return _mdToHtml($page, "./src/page/contribute.md?t=" + ___date_version___);
    },
    "faq": function () {
        return _mdToHtml($page, "./src/page/faq.md?t=" + ___date_version___);
    },
    "sponsor": function () {
        return _mdToHtml($page, "./src/page/sponsor.md?t=" + ___date_version___);
    }
};
